define([], function () {
    'use strict';

    var environment = {};

    environment.parseEnvFromPcastBaseUri = function (uri) {
        uri = uri.toLowerCase();

        if (uri.indexOf('local') > -1) {
            return 'local';
        } else if (uri.indexOf('stg') > -1) {
            return 'staging';
        }

        return 'production';
    };

    return environment;
});


